body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.App {
  text-align: center;
}


.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
header {
    height: 100vh;
    width: 100%;
    left: 0;
    top: 0;
    position: relative;
    background-color:  #7F3F92;
    font-family: 'ZCOOL KuaiLe', cursive;
}

header p{
    margin: 0;
    color: #3A1D31;
    font-size: 4em;
    font-weight: bold;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
body {
    background-color: #F3E7EF;
}
h1{
    font-family: 'ZCOOL KuaiLe', cursive;
}
.about {
    max-width: 95%;
    margin-left: auto;
    margin-right: auto;
    left:0;
    right: 0;
    overflow: hidden;
    
    
}
.picWithText {
    max-width: 25%;
    float: left;
}
.profilePic img{   
    height: auto;
    width: 100%;;
    background-position: center center;
    background-repeat: no-repeat;
    height: auto;
    border-radius: 0 50% 50% 0 ;
}

.textDiv {
    padding-top: 1em;
}

.textDiv p {
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%); 
}
.skillset {
    float: right;
    min-width: 60%;
}
.container {
    width: 100%;
    background-color: #ddd;
  }

.skills {
    text-align: right;
    padding: 10px;
    color: white;
  }
  .front {width: 80%; background-color: #4CAF50;}
  .back {width: 60%; background-color: #4CAF50;}
  .rpa {width: 60%; background-color: #4CAF50;}
  .embedded {width: 60%; background-color: #4CAF50;}
  .devops {width: 40%; background-color: #4CAF50;}

.projectList {
    max-width: 90%;
    margin: auto;
    background-color: #F3E7EF;
    margin-bottom: 2em;
}
a {
    color:black;
}
h1 {
    text-align: center;
}
.projectBox {
    overflow: hidden;
    margin-left: 1em;
}


.projectImage {
    width: 10em;
    height: auto;
    float: left;
    border-radius: 50%;
}

.slick-arrow{
    background-color: #7F3F92;
    border-radius: 50%;
}
.footer {
    background-color: #7F3F92;
    color:white;
    margin-bottom: 0;
    float: left;
    min-width: 100%;
}
br {
    display:block;
    margin: 10px 0;
}
.contactInfo a{
    text-align: center;
    line-height: 1px;
}
